import { SET_MARKER_POSITION } from "../actions";

const INITIAL_STATE = {
  marker: null,
};

export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case SET_MARKER_POSITION:
      return {
        ...state,
        marker: payload,
      };
    default:
      return state;
  }
};
