import React, { useState, useMemo, useEffect } from "react";
import { connect } from "react-redux";
import { useLocation, useHistory } from "react-router-dom";
import { useGeolocation } from "react-use";
import { Header, Icon, Image, Menu, Segment, Sidebar } from "semantic-ui-react";

import MapView from "./MapView";
import TopSheet from "./TopSheet";
import BottomSheet from "./BottomSheet";

const App = ({ events, preferences }) => {
  const [currentPosition, setCurrentPosition] = useState(null);
  const location = useGeolocation({
    enableHighAccuracy: false,
    maximumAge: 15000,
    timeout: 30000,
  });

  useEffect(() => {
    if (!location.loading && location.longitude) {
      setCurrentPosition([location.longitude, location.latitude]);
    }
  }, [location]);

  const [controlsVisible, setControlsVisible] = useState(true);
  const url = useLocation();
  const history = useHistory();
  const menuItems = useMemo(
    () => [
      {
        key: "nearby",
        name: "Nearby",
        path: "/events",
        active: url.pathname === "/events",
      },
      {
        key: "report",
        name: "Report",
        path: "/report",
        active: url.pathname === "/report",
      },
      {
        key: "sos",
        name: "SOS",
        path: "/sos",
        active: url.pathname === "/sos",
      },
    ],
    [url]
  );

  const handleMenuItemClick = (event, data) => {
    const { path } = data;
    setControlsVisible(true);
    history.push(path);
  };

  const handleMapMove = (mapEvent) => {
    const { lng: lon, lat } = mapEvent.getCenter();
    // setMapPosition([lon, lat]);
  };

  const handleMapContextMenu = (mapEvent) => {
    console.log(mapEvent);
  };

  const handleToggleControls = (visible) => {
    if (typeof visible === "boolean") return setControlsVisible(visible);
    setControlsVisible((v) => !v);
  };

  return (
    <>
      <Sidebar.Pushable className="no-overflow">
        <TopSheet visible={controlsVisible} />
        <BottomSheet visible={controlsVisible} />
        <Sidebar.Pusher>
          <MapView
            center={currentPosition ?? preferences.defaultPosition}
            position={currentPosition}
            onMoveEnd={handleMapMove}
            toggleControls={handleToggleControls}
            onContextMenu={handleMapContextMenu}
          />
        </Sidebar.Pusher>
      </Sidebar.Pushable>
      <Menu
        pointing
        inverted
        fixed="bottom"
        size="large"
        widths={3}
        onItemClick={handleMenuItemClick}
        items={menuItems}
      />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    events: state.events,
    preferences: state.preferences,
  };
};

export default connect(mapStateToProps, {})(App);
